.footer {
  background: #E5E5E5;
  position: fixed;
  bottom:0;
  left:0;
  width:100%;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #E9E4D0;
  text-align: center;
  font-size: 20px;
}



